.calendar {
  margin: 0 auto;
  padding: 50px;
  display: flex;
  flex-wrap: wrap;
  gap: $gap;
  width: $calendarWidth;

  @each $name, $dayNumber in $days {
    &--start-day--#{$name} &__day:first-child {
      margin-left: getMargin($dayNumber, $cellSize, $gap);
    }
  }

  @for $index from 28 through 31 {
    &--month-length-#{$index} &__day:nth-child(n + #{$index + 1}) {
      display: none;
    }
  }
}
